.frontHeader {
  box-shadow: 0 0 10px #bbb;
  padding: 20px;
  position: fixed;
  left: 0;
  z-index: 99;
  top: 0;
  width: 100%;
  background: #fff; }
  .frontHeader .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
    flex-wrap: wrap; }
    .frontHeader .container .logo {
      max-width: 200px; }
    .frontHeader .container .lang-switch {
      background-color: #5832E0;
      border: 0;
      padding: 3px;
      color: #fff; }
    .frontHeader .container p {
      margin: 0; }
    .frontHeader .container .foot-menu span {
      margin: 0 15px; }
  .frontHeader .mob-menu {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 80%;
    justify-content: flex-start;
    max-width: 250px;
    position: fixed;
    background: #efefef;
    padding: 10px 20px !important;
    right: -250px;
    opacity: 0;
    transition: all 0.3s ease;
    height: 100%;
    top: 50px;
    overflow: auto; }
    .frontHeader .mob-menu .pointer {
      margin: 10px 0; }
    .frontHeader .mob-menu button {
      margin-left: 0 !important; }
  @media screen and (max-width: 767px) {
    .frontHeader {
      padding: 10px; }
      .frontHeader .container {
        text-align: center; } }
